/* history.css */
.history {
    position: relative;
    text-align: center;
    overflow: hidden;
    height: 100%;
    width: 100%;
    left: 0;
    bottom: -10px;
    background: white;
}


.containerHistory{
    position: relative;
    /*padding: 20px 0 50px 0;*/
    margin: 0;
    display: grid;
    z-index: 1;
    gap: 0;
    width: 100%;
    height: 100%;
    grid-template-rows: repeat(3,1fr);
    grid-template-columns: 1fr;

}

.separator{
    position: relative;
    top:20px;
    height: 93px;
    z-index: 1;
    background: #F7F7F7;
}
