.section,
.section * {
    box-sizing: border-box;
}
.section {
    padding-left: 80px;
    padding-top: 80px;
    padding-right: 80px;
    display: flex;
    flex-direction: column;
    gap: 2px;
    align-items: center;
    justify-content: end;
    height: 100%;
    position: relative;
    transform-origin: 0 0;
    z-index: 1;
    transform: rotate(0deg) scale(1, 1);
}
.caption {
    color: #001d6c;
    text-align: center;
    font-family: "Roboto", Helvetica;
    font-size: 20px;
    line-height: 100%;
    letter-spacing: 1px;
    font-weight: 700;
    text-transform: uppercase;
    position: relative;
    align-self: stretch;
}
.sectionText {
    display: flex;
    flex-direction: column;
    gap: 18px;
    align-items: center;
    justify-content: flex-start;
    flex-shrink: 0;
    width: 100%;
    position: relative;
}
.top {
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: center;
    justify-content: flex-start;
    align-self: stretch;
    flex-shrink: 0;
    position: relative;
}
.secondaryHeadline {
    color: #21272a;
    text-align: center;
    font-family: "Roboto", Helvetica;
    font-size: 42px;
    line-height: 110%;
    font-weight: 700;
    position: relative;
    align-self: stretch;
}
.vision{
    font-weight: 800;
    font-family: "Roboto", Helvetica;
}
.paragraph {
    color:  #21272a;
    text-align: left;
    font-family: "Roboto", Helvetica;
    font-size: 18px;
    line-height: 140%;
    font-weight: 400;
    position: relative;
    align-self: stretch;
    height: 161px;
}

.subdivideLine,
.subdivideLine * {
    box-sizing: border-box;
}
.subdivideLine {
    height: 86px;
    position: absolute;
    object-fit: cover;
    width: 100%;
    z-index: 2;
    bottom: 20px;
    overflow: visible;
}

.subdivideLine1,
.subdivideLine1 * {
    box-sizing: border-box;
}
.subdivideLine1 {
    height: 86px;
    position: absolute;
    object-fit: cover;
    width: 100%;
    z-index: 2;
    top: 5px;
    overflow: visible;
}


.vector,
.vector * {
    box-sizing: border-box;
}
.vector {
    height: 76px;
    position: absolute;
    overflow: visible;
    object-fit: cover;
    width: 100%;
    z-index: 1;
    bottom: -2px;
}

.vector1,
.vector1 * {
    box-sizing: border-box;
}
.vector1 {
    height: 76px;
    position: absolute;
    overflow: visible;
    object-fit: cover;
    width: 100%;
    z-index: 1;
    top: -10px;
}

/* Mobile styles */
@media (max-width: 768px) {
    .paragraph{
        height: 289px;
    }
    /* history.css */
    .history {
        position: relative;
        text-align: center;
        overflow: hidden;
        height: 100%;
        top: 0;
        left: 0;
        background: white;
    }
    .section{
        height: 100%;
        padding: 20px;
        justify-content: center;
    }

    .mask {
        z-index: 1;
        height: 100%;
        width: 100%;
    }
    .separator{
        position: relative;
        top: 139px;
        height: 50px;
    }
    .top3 {
        justify-content: flex-end;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 8px;
        position: relative;
        align-self: stretch;
        width: 100%;
        padding: 40px 40px 0 40px;
        flex: 0 0 auto;
    }
    .historyVideo{
        height: 100%;
        width: 100%;
        object-fit: fill;
    }

}

